<template>
    <div  v-if="seen" v-bind:title="message" class="newItem">
        <p v-on:click="updateMessage" class="bg-warning">{{content}}</p>
        <button v-on:click="updateMessage" class="btn btn-primary btn-sm" > Update Messeage</button>
    </div>
</template>


<script>

export default {
    name: "newItem", 
    data() {
        return {
            seen: true,
            message: "Title Description using v-bind", 
            content: "This is a Paragraph"
        }
    
    }, 
    methods: {
        updateMessage: function(){
            this.content = "Now edited!"
        }
    }
}
</script>

<style scoped>

    .newItem {
        color: slategrey;
    }
</style>



