        },
        calculateContourField: function() {
            this.contourPoints = [];    
            var anchorsLocal = this.anchorManual;
            var ClampOffset = .5;
            var pi;
            for (pi = 0; pi < this.contourGrid.length; pi++) {
                var p = this.contourGrid[pi];
                var beffect = 0;
                var reffect = 0;
                var ai;
                for (ai = 0; ai < anchorsLocal.length; ai++) {
                    var a = anchorsLocal[ai].Data;
                    var dx2 = Math.pow((a.x - p.x),2);
                    var dy2 = Math.pow((a.y - p.y),2);
                    var dist = Math.pow((dx2+dy2),0.5);
                    if (dist == 0) {
                        beffect = 0.000001;
                        reffect = a.v;
                        break;
                    }
                    var effect = Math.pow(dist,-3); // magnetic field?
                    
                    reffect += a.v * effect;
                    beffect += (1 - a.v) * effect;
                }
                var ref = reffect / (reffect+beffect);
                p.v = this.fit(ref, 0.5-ClampOffset, 0.5+ClampOffset, 0, 1);

                if (p.v >= this.ThresholdAt - this.ThresholdWidth && p.v <= this.ThresholdAt + this.ThresholdWidth){
                    this.contourPoints.push(p);
                }
            }
            this.calcCount += 1
            
        },
        evalIntensity: function(px, py, anchorsLocal) {
            var ClampOffset = .5;
            var beffect = 0;
            var reffect = 0;
            var ai;
            for (ai = 0; ai < anchorsLocal.length; ai++) {
                var a = anchorsLocal[ai].Data;
                var dx2 = Math.pow((a.x - px),2);
                var dy2 = Math.pow((a.y - py),2);
                var dist = Math.pow((dx2+dy2),0.5);
                if (dist == 0) {
                    beffect = 0.000001;
                    reffect = a.v;
                    break;
                        }
                var effect = Math.pow(dist,-3);
                        
                reffect += a.v * effect;
                beffect += (1 - a.v) * effect;
            }
            var ref = reffect / (reffect+beffect);
            var pv = this.fit(ref, 0.5-ClampOffset, 0.5+ClampOffset, 0, 1);
            return pv
        },
        drawThresholds2d: function(tol) {
            //getting the updated anchor position
            var anchorsLocal = this.anchorManual;
            //finding the bounding box
            var xmin = 0;
            var xmax = 0;
            var ymin = 0;
            var ymax = 0;
            if (anchorsLocal.length > 0) {
                xmin = anchorsLocal[0].Data.x;
                xmax = anchorsLocal[0].Data.x;
                ymin = anchorsLocal[0].Data.y;
                ymax = anchorsLocal[0].Data.y;
                for( var i=0; i<anchorsLocal.length; i++) {
                    if (anchorsLocal[i].Data.x > xmax) xmax = anchorsLocal[i].Data.x;
                    if (anchorsLocal[i].Data.x < xmin) xmin = anchorsLocal[i].Data.x;
                    if (anchorsLocal[i].Data.y > ymax) ymax = anchorsLocal[i].Data.y;
                    if (anchorsLocal[i].Data.y < ymin) ymin = anchorsLocal[i].Data.y;
                }
                xmin -= 2;
                ymin -= 2;
                xmax += 2;
                ymax += 2;
            }

            //creating the base grid
            var xdif = (xmax - xmin)/tol;
            var ydif = (ymax - ymin)/tol;

            // positions list 
            this.thresholdPoints = [];
			var id = 1000;
  

            for (var i=0; i<xdif; i++){
                for (var j=0; j<ydif; j++){
                    id++;
                    var px = i*tol;
                    var py = j*tol;
                    var pv = this.evalIntensity(px, py, anchorsLocal);

                    // X forward testing
                    var pv_xf = this.evalIntensity(px+tol, py, anchorsLocal);
                    if ((pv - 0.5)*(pv_xf - 0.5)<=0) {
                        var threshPos = pv<pv_xf ? this.fit(0.5, pv, pv_xf, 0, 1) : this.fit(0.5, pv_xf, pv, 1, 0)
                        var new_px = px + threshPos*tol;
                        var new_py = py;
                        id++;

                        var newObj = {
                        id: id,
                        x: new_px,
                        y: new_py,
                        //v: this.evalIntensity(new_px, new_pypy, anchorsLocal), // is this necessary?
                        };
                        this.thresholdPoints.push(newObj);
                    }
                    // Y forward testing
                    var pv_yf = this.evalIntensity(px, py+tol, anchorsLocal);
                    if ((pv - 0.5)*(pv_yf - 0.5)<=0) {
                        var threshPos = pv<pv_yf ? this.fit(0.5, pv, pv_yf, 0, 1) : this.fit(0.5, pv_yf, pv, 1, 0)
                        var new_py = py + threshPos*tol;
                        var new_px = px;
                        id++;

                        var newObj = {
                        id: id,
                        x: new_px,
                        y: new_py,
                        };
                        this.thresholdPoints.push(newObj);

                    }
                }
            }
            
            
        },
        calculateContourGrid: function(){
            const width = this.WIDTH();
            const height = this.HEIGHT()
            const q = 5; // The level of detail, e.g., sample every 4 pixels in x and y.
            const xShift = width  * 0.065; // starting point in pixels!
            const yShift = height * 0.57;
            const xSpan  = width  * 0.85;
            const ySpan  = height * 0.2;
            const x0 = -q / 2, x1 = xSpan + 28 + q;
            const y0 = -q / 2, y1 = ySpan + q;
            const n = Math.ceil((x1 - x0) / q);
            const m = Math.ceil((y1 - y0) / q);
            const anchorsLocal = this.anchorManual;

            const grid = new Array(n * m);
            for (let j = 0; j < m; ++j) {
                for (let i = 0; i < n; ++i) {
                grid[j * n + i] = this.evalIntensity(i * q + x0 + xShift, j * q + y0 + yShift, anchorsLocal);
                }
            }
            grid.x = -q;
            grid.y = -q;
            grid.k = q;
            grid.n = n;
            grid.m = m;
            grid.xShift = xShift;
            grid.yShift = yShift;

            return grid;

        },
        generateContourGeo: function(){
            // returns d3 contours function/object
            const grid = this.calculateContourGrid();
            const thresholds = [0.25, 0.5, 0.75];

            // Converts from grid coordinates (indexes) to screen coordinates (pixels).
            var transform = ({type, value, coordinates}) => {
                        return {type, value, coordinates: coordinates.map(rings => {
                            return rings.map(points => {
                            return points.map(([x, y]) => ([

                                grid.x + grid.k * x + grid.xShift,
                                grid.y + grid.k * y + grid.yShift

                                ]));
                            });
                        })};
            }

            const contours = d3.contours()
                .size([grid.n, grid.m])
                .thresholds(thresholds)
              (grid)
              .map(transform);

            return contours;
            
        },

        drawBaseGrid: function() {

            const svg = d3.select('.grid_svg');
            const gridPoints = this.grid;
            // const scaleFacetor = this.siteImageScaleF;
            // const transform = this.globalTransform;

            const g = svg.select("#siteAnchorsGrid")

            g.selectAll("circle")
              .data(gridPoints).join("circle")
                .attr('cx', p => p.x ) 
                .attr('cy', p => p.y) 
                .attr('r', 1.5)
                .attr('fill', 'black')
                .attr("fill-opacity", 0.05)
                // .attr("stroke-width", "0.25")
                // .attr("transform", transform)
                .on("click",p => this.handleClickPoint(p));

        },

        drawBorders: function() {

            const svg = d3.select('.grid_svg');
            const gridPoints = this.borders;
            // const scaleFacetor = this.siteImageScaleF;
            // const transform = this.globalTransform;

            const g = svg.select("#siteBorders")

            g.selectAll("circle")
              .data(gridPoints).join("circle")
                .attr('cx', p => p.x ) 
                .attr('cy', p => p.y) 
                .attr('r', 0.5)
                .attr('fill', 'black')
                .attr("fill-opacity", 0.7)
                // .attr("stroke-width", "0.25")
                // .attr("transform", transform)
                // .on("click",p => this.handleClickPoint(p));

        },

        drawAnchors: function() {
            const anchors = this.anchorManual; // this.anchorManualInteractive

            const g = d3.select(".anchors")

            g.selectAll("circle")
              .data(anchors)
                .join("circle")
                .attr('cx', a => (a.Data.x)) 
                .attr('cy', a => (a.Data.y))
                .attr('r', 2)
                .attr('stroke-width',  0.7)
                .attr('stroke',  a => a.Data.color)
                .attr('fill',  "none")
                .on('click', a => this.handleClickAnchor(a.Data)); 
                // .call(d3.drag()
                //     .on("start", dragstarted)
                //     .on("drag", dragged)
                //     .on("end", dragended)); 

            function dragstarted(d) {
                d3.select(this).raise().classed("active", true);
            }

            function dragged(d) {
                // this changes position on the screen : snapp added
                var pX = Math.ceil(d3.event.x / ((nrad+space)*2));
                var pY = Math.ceil(d3.event.y / ((nrad+space)*2)) - 1;
                /* old shifted grid
                var pX = Math.ceil((numi-2)*0.5 + d3.event.x / ((nrad+space)*2));
                var pY = Math.ceil(numj*0.5 + d3.event.y / ((nrad+space)*2)) - 1;
                */
                d3.select(this).attr("cx", d.x = (pX) * ((nrad+space)*2)).attr("cy", d.y = (pY) * ((nrad+space)*2));
            }

            function dragended(d) {

                var a = d.Data;
                var aKey = d.Key ; // does this work? yes

                // change coordination back to database scale!!
                var pX = d3.event.x ;
                var pY = d3.event.y ;
                /* old shifted grid
                var pX = Math.ceil((numi-2)*0.5 + d3.event.x / ((nrad+space)*2));
                var pY = Math.ceil(numj*0.5 + d3.event.y / ((nrad+space)*2)) - 1;
                */
                // console.log("supposed positions ", pX, pY);
                a.x = pX;
                a.y = pY;
                //update on server
                anchorsRef.child(aKey).set(a);

                //console.log(" and mouse is on ",d3.event.x, d3.event.y );
                
                d3.select(this).classed("active", false); // is this line here or out?
            }
        },

        drawColumns: function(){

            const columnsG = d3.select('#siteColumns')
            var columns = this.columns;
            // const transform = this.globalTransform;

            columnsG.selectAll("circle")
                .data(columns)
                .enter().append("circle")
                    .attr("cx", p => p.x  ) 
                    .attr("cy", p => p.y  )
                    .attr('r', 5)
                    .attr('fill', 'white')
                    .attr('class', 'columns')
                    .attr('stroke-width', 1)
                    .attr('stroke', 'black')
                    // .attr('transform', transform)
        },

        drawColumnsArea: function(){

            const areasG   = d3.select("#areas-text")
            var columns = this.columns;

            areasG.selectAll("text")
                .data(columns)
                .join("text")
                    .attr('x', p => p.x - 3.5 )
                    .attr('y', p => p.y + 1.4 )
                    .text( d => Math.ceil(d.area) )
                    .attr("font-family", "sans-serif")
                    .attr("font-size", "3px")
                    .attr("fill", "grey")

        },

        appendContourGeoToSVG: function(){

            const svg = d3.select('.grid_svg');
            const contours = this.generateContourGeo();

            // drawing methods
            var path = d3.geoPath();
            var line = d3.line()
                .x((d) => d[0])
                .y((d) => d[1])
                // .curve(d3.curveBundle.beta(1))
                .curve(d3.curveBasisClosed);
            
            var flattenCountours = function(coordinates){
                var dText = "";
                for(var i=0; i< coordinates.length; i++){
                    var contour = coordinates[i]; // a countour can be made from more than one shape, a hole in the middle
                    for(var j=0; j<contour.length; j++){
                        dText += line(contour[j]);
                    }
                }
                return dText;
            }

            svg.select(".d3-contours")
                .attr("fill", "none")
                .attr("stroke-opacity", 1)
              .selectAll("path")
              .data(contours)
              .join("path")
                .attr("fill", d => this.setHexColor(d.value))
                .attr("fill-opacity", 0.3)
                .attr('stroke-width', 0.5)
                .attr("stroke", "black") //d => this.setHexColor(d.value))
                // .attr("d", path) // this is the old contour "unsmooth"
                .attr("d", d => flattenCountours(d.coordinates)); // smoothed contour

        },

        appendContourGeoToSVG2: function(){

            const svg = d3.select('.grid_svg');//.call(zoom);
            const numi = this.numi;
            const numj = this.numj;
            const nrad = this.nrad;
            const space = this.space;

            const contours = this.thresholdPoints;

            const g = svg.select(".d3-dots")
                            .attr("fill", "#555")
                            // .attr("stroke", "#fff");

            g.selectAll("circle")
                .data(contours).join("circle")
                /* old shifted grid
                .attr('cx', p => (p.x-(numi-2)/2)*((nrad) + space)*2)
                .attr('cy', p => (p.y-numj/2)*((nrad) + space)*2) */
                .attr('cx', p => p.x)
                .attr('cy', p => p.y)
                .attr('r', '1')
                //.attr("fill", d => color(d.value))
                .attr("d", d3.geoPath()); // did I mess this up

        },
        calcVoronoiCells : function(){
            // console.log("____ voronoi function")
            const width  = this.WIDTH();
            const height = this.HEIGHT();
            const vm = this;

            var voronoi = d3Voronoi.voronoi()
                                    .extent([[50, 380], [width - 100, height - 150]])
                                    .x( d => d.x)
                                    .y( d => d.y);


            var anchors = this.anchorDataManual //this.anchorManualInteractive;
            const columns = this.columns;
            const borders = this.borders;
            var anchorsAndColumns = anchors.concat(columns, borders);
            var   diagram = voronoi(anchorsAndColumns);
            var   cells   = diagram.polygons();

            
            // console.log("cell", cells[0])
            // var vertices = this.cellToVertices(cells[0]);
            // console.log("vertices: ", vertices)
            // var area = this.calcPolygonArea(vertices);
            // console.log(area)

            // A R E A S
            // players
            var cellsByPlayer = {}
            var areasByPlayer = {}
            for(var i=0; i<this.allPlayers.length; i++){
                var player = this.allPlayers[i]
                
                cellsByPlayer[player] = []
                areasByPlayer[player] = 0
            }

            for(var i=0; i<columns.length; i++){
                this.columns[i].area = 0;
                        }

            for(var i=0; i<anchors.length; i++){
                var cell = cells[i];
                var owner = cell.data.owner;
                cellsByPlayer[owner].push(cell);

                var cellColumn = cell.data.column;
                var vertices = this.cellToVertices(cell);
                var area = this.calcPolygonArea(vertices);
                cell.data.column.area += area;
                areasByPlayer[owner] += area;
            }

            // columns
            // one loop is enough here! 
            // for(var i=0; i<columns.length; i++){
            //     this.columns[i].area = 0;
            //     var columnId = this.columns[i].id;

            //     for(var j=0; j<anchors.length; j++){
            //         var cell = cells[j];
            //         var cellColumnId = cell.data.column.id;
            //         if(columnId == cellColumnId){
            //             var vertices = this.cellToVertices(cell);
            //             var area = this.calcPolygonArea(vertices);
            //             this.columns[i].area += area;
            //         }
            //     }
            // }

            this.userTotalArea = areasByPlayer[this.currentUser]
            this.renderTotalArea();
            // console.log(this.currentUser, "total area", areasByPlayer[this.currentUser])

            return cells;
        },

        renderTotalArea: function(){
            const g = d3.select("#total-area");
            const text = "Total Area: " + Math.floor(this.userTotalArea) ;
            const color = this.userColor;
            g.selectAll("text")
                .data([text])
                .join("text")
                .text(text)
                .attr("fill", color )
                .attr("font-family", "sans-serif")
                .attr("font-size", "18px")
                // .attr("fill", "lightgrey")
        },


        drawVoronoi : function(){

            // DRAWING ANCHORS ONLY
            var anchors = this.anchorDataManual 
            var cells = this.calcVoronoiCells();
            // adding each cell to its anchor
            for(var i=0; i<anchors.length; i++){
                var anchor = anchors[i];
                if(cells[i]){
                    anchor.cell = cells[i];
                    }
                // anchor.color = anchor.Data.color
            }

            var line = d3.line()
                // .x((d) => d[0]).y((d) => d[1])
                // .curve(d3.curveLinear);
                // .curve(d3.curveBasisClosed);

            var cellsG = d3.select("#voronoi-cells")//.on("touchmove mousemove", moved)

            cellsG = cellsG.selectAll("path")
                        .data(anchors)
                        .join("path")
                          .call(redrawCell);


            function redrawCell(cell){
                cell
                // .attr('stroke-width', 3)
                // .attr('stroke-opacity', 0.7)
                .attr("stroke", "none")
                .attr('fill-opacity', 0.4)
                .attr("fill", a => a.color)                
                .attr("d", d => line(d.cell) + "Z" );

                function anchorColor(a){
                    if(a.color){
                        return a.color;
                    }
                    else{
                        return "none"
                    }
                }

            }

            function redraw(){
                // var diagram = voronoi(sites);

                var   diagram = voronoi(anchors);
                var   cells   = diagram.polygons();

                // adding each cell to its anchor
                for(var i=0; i<anchors.length; i++){
                    var anchor = anchors[i];
                    anchor.cell = cells[i];
                }

                // cellsG = cellsG.data(anchors).join("path").call(redrawCell);
                cellsG = cellsG.data(anchors), cellsG.exit().remove();
                cellsG = cellsG.enter().append("path").merge(cellsG).call(redrawCell);
            }

            function moved() {
                // console.log("Id from moved is: ", vm.curAnchorID)

                if(vm.curAnchorID > 0){

                    var a = anchors[vm.curAnchorIndex].Data;
                    a.x = d3.mouse(this)[0];
                    a.y = d3.mouse(this)[1];
                    redraw();
                    }
                }


            // console.log("____ voronoi function SUCCESSFUL ! ")
            // **********************************************************
        },

        ZOOM: function(){

            const width = this.WIDTH();
            const height = this.HEIGHT();

            const zoom = d3.zoom()
                    .scaleExtent([1, 5])
                    .translateExtent([[-width,-height], [width,height]])
                    //.extent([[-width*0.5,-height*0.5], [width*0.5,height*0.5]])
                    .on("zoom", zoomed);

            const svg = d3.select('.grid_svg').call(zoom);
            const g   = d3.select(".zoomG") // what g to apply the zoom to!

            function zoomed(){
                    g.attr("transform", d3.event.transform);
                }  
        },

        WIDTH: function() {
            return 7948 * this.siteImageScaleF; // (window.innerWidth)
        },
        HEIGHT: function() {
            return 3193 * this.siteImageScaleF; //(window.innerHeight)
        },
        viewBox: function() {
            let str = (String(-this.WIDTH()/2)+' '+String(-this.HEIGHT()/2)+' '+String(this.WIDTH())+' '+String(this.HEIGHT()))
            return str
        },

        calculateDistance: function(p0, p1){
            var dx = p1.x - p0.x;
            var dy = p1.y - p0.y;
            return Math.sqrt( dx * dx + dy * dy );
        },

        cellToVertices: function(cell){
            var vertices = [];
            for(var i=0; i<cell.length; i++){
                vertices.push({
                    x: cell[i][0],
                    y: cell[i][1]
                })
            }
            // console.log("vertices: ", vertices)
            return vertices;

        },

        calcPolygonArea: function(vertices) {
            var total = 0;

            for (var i = 0, l = vertices.length; i < l; i++) {
            var addX = vertices[i].x;
            var addY = vertices[i == vertices.length - 1 ? 0 : i + 1].y;
            var subX = vertices[i == vertices.length - 1 ? 0 : i + 1].x;
            var subY = vertices[i].y;

            total += (addX * addY * 0.5);
            total -= (subX * subY * 0.5);
            }

            return Math.abs(total);
        }

    }

}
</script>

<style>
#picture {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    position: absolute;
}
path, circle {
    transition: all 0ms ease;
}

/* path {
    stroke-linejoin: round;
    stroke-miterlimit: 2;
    stroke-linecap: round;
} */

</style>



